import { Container, TableBody, TableCell, TableRow, Toolbar } from '@material-ui/core';
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import useTable from './useTable';
import Controls from './Controls';

export default function Orders() {

    const headCells = [
        {id: 'id', label: 'Order #'},
        {id: 'id2', label: 'Pizza #'},
        {id: 'descrp', label: 'Size'},
        {id: 'descrp', label: 'Crust'},
        {id: 'descrp', label: 'Type'}
    ];

    const [orders, setOrders] = useState([])

    const [searchTerm, setSearchTerm] = useState('')

    const {
        TableContainer,
        TblHead
    } = useTable(orders, headCells);


    useEffect(() => {
        axios.get('/api/orders')
            .then(response => setOrders(response.data));
    }, []);

    return (
        <Container>
            <h1 className="orderHeader" id="order">Orders</h1>
            <Toolbar>
                <Controls.Input
                    label="Search Order"
                    style={{ width: '100%' }}
                    onChange={event => {setSearchTerm(event.target.value)}}
                />    
            </Toolbar>
            <TableContainer>
                <TblHead />
                <TableBody>
                    {
                        orders.filter((val) => {
                            if (searchTerm == "") {
                                return val
                            } else if (val.title.toLowerCase().includes(searchTerm.toLowerCase())) {
                                return val
                            }
                        }).map(order => 
                            (<TableRow key={order.pizza_id}>
                                <TableCell>{order.order_number}</TableCell>
                                <TableCell>{order.pizza_number}</TableCell>
                                <TableCell>{order.size}</TableCell>
                                <TableCell>{order.crust}</TableCell>
                                <TableCell>{order.type}</TableCell>
                            </TableRow>))
                    }
                </TableBody>
            </TableContainer>
        </Container>
    );
}